
import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getOrders, getOrdersCalculations } from '../../../actions/orderAction'

export default function OrderStats() {
    const [orders, setOrders] = useState([
        {
            count: 0,
            pendingOrders: 0,
            acceptedOrders: 0,
            startedOrders: 0,
            completedOrders: 0,
            rejectedOrders: 0,
        }
    ])
    const dispatch = useDispatch()
    const { count, startedOrders } = useSelector(state => state)
    const countries = ['United States', 'Canada']
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        const ors = []
        countries.map(country => {
            getOrdersCalculations(country)
                .then(res => { ors.push(res) })
                .catch(err => { console.log(err) })
                .finally(() => {
                    setOrders(ors)
                    setLoading(true)
                    console.log(ors)
                })
        })

    }, [])

    return (
        <div className="ibox">
            <div className="ibox-title" style={{ minHeight: 84 }}>
                <h5>Order Statistics</h5>
                <div className='ibox-tools'>
                    <div className='custom-ibox-tools'>
                        <span className='text-uppercase'>Total Orders</span>
                    </div>
                    {
                        loading && orders.map((order, i) => (
                            <div className='custom-ibox-tools' key={i}>
                                <span>{countries[i]}:  </span>
                                <span>{order.count}</span>
                            </div>
                        ))
                    }
                </div>
            </div>

            <div className="card card-dashboard-table">
                <table className="table table-bordered">
                    <thead>
                        <tr>
                            <th>Country</th>
                            <th className='bg-primary'>Accepted</th>
                            <th className='bg-warning' >Pending</th>
                            <th className='bg-info' >Running</th>
                            <th className='bg-success' >Completed</th>
                            <th className='bg-danger' >Rejected</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            loading && orders.map((country, i) => (
                                <tr key={i}>
                                    <td><a href="#">{countries[i]}</a></td>
                                    <td><strong>{country.acceptedOrders}</strong></td>
                                    <td><strong>{country.pendingOrders}</strong></td>
                                    <td><strong>{country.startedOrders}</strong></td>
                                    <td><strong>{country.completedOrders}</strong></td>
                                    <td><strong>{country.rejectedOrders}</strong></td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}
